/* src/index.css */

:root {
  --main-bg: #1A1B26;
  --secondary-bg: #24283B;
  --card-bg: #1F2335;
  --hover-bg: #2A2F41;

  --text-primary: #C0CAF5;
  --text-secondary: #A9B1D6;
  --text-muted: #565F89;

  --accent-blue: #7AA2F7;
  --accent-green: #9ECE6A;
  --accent-purple: #BB9AF7;
  --accent-pink: #F7768E;
  --accent-orange: #FF9E64;
  --accent-red: #f7768e;
  
  --border-color: #414868;
  --shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  --border-radius: 8px;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: var(--main-bg);
  color: var(--text-primary);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Add this new section to the bottom of the file */

/* Dark Scrollbar for the entire application */
body::-webkit-scrollbar {
  width: 12px;
}

body::-webkit-scrollbar-track {
  background: var(--main-bg); /* Use your app's main background color */
}

body::-webkit-scrollbar-thumb {
  background-color: #565f89; /* A dark color from your theme */
  border-radius: 6px;
  border: 3px solid var(--main-bg); /* Creates a nice padding effect */
}

body::-webkit-scrollbar-thumb:hover {
  background-color: #7AA2F7; /* A brighter color on hover */
}