/* src/styles/Dashboard.css */

.dashboard-container {
  padding: 2rem;
  max-width: 1600px;
  margin: 0 auto;
}

.overview-header {
  margin-bottom: 2rem;
  padding-bottom: 1rem;
  border-bottom: 1px solid #414868;
}

.main-title {
  font-size: 2rem;
  color: #c0caf5;
  margin: 0 0 0.5rem 0;
}

.welcome-message {
  font-size: 1rem;
  color: #a9b1d6;
  margin: 0;
}

/* --- Hiring Pipeline Section --- */
.hiring-pipeline-section {
  background-color: #24283b;
  padding: 1.5rem;
  border-radius: 8px;
  margin-bottom: 2.5rem;
  border: 1px solid #414868;
}

.section-title {
  color: #c0caf5;
  font-size: 1.25rem;
  margin-top: 0;
  margin-bottom: 1.5rem;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.pipeline-table {
  width: 100%;
  border-collapse: collapse;
}

.pipeline-table th,
.pipeline-table td {
  padding: 0.75rem;
  text-align: center;
  border-bottom: 1px solid #2a2f4e;
  color: #a9b1d6;
  font-size: 0.9rem;
}

.pipeline-table th {
  color: #a9b1d6;
  font-weight: 600;
  text-transform: uppercase;
  font-size: 0.8rem;
  border-bottom: 2px solid #414868;
}

.pipeline-table tbody tr:hover {
  background-color: #2a2f4e;
}

.pipeline-table td:first-child {
    text-align: left;
}

.pipeline-table td strong {
  color: #c0caf5;
}

.pipeline-table .position-title-group {
    display: flex;
    flex-direction: column;
}
.pipeline-table .position-title-group span {
    font-size: 0.8em;
    color: #565f89;
}

/* ** THE FIX: New styles for outline badges ** */
.stage-count {
  display: inline-flex; /* Use inline-flex for proper alignment */
  align-items: center;
  gap: 0.35rem;
  justify-content: center;
  background-color: transparent; /* No background */
  padding: 0.15rem 0.6rem;
  border-radius: 12px;
  font-weight: bold;
  border: 2px solid #414868; /* Default border color */
  color: #c0caf5; /* Default text color */
  min-width: 28px;
}

.pipeline-table td:nth-child(2) .stage-count { border-color: #565f89; color: #565f89; }
.pipeline-table td:nth-child(3) .stage-count { border-color: #bb9af7; color: #bb9af7; }
.pipeline-table td:nth-child(4) .stage-count { border-color: #7aa2f7; color: #7aa2f7; }
.pipeline-table td:nth-child(5) .stage-count { border-color: #73daca; color: #73daca; }
.pipeline-table td:nth-child(6) .stage-count { border-color: #e0af68; color: #e0af68; }
.pipeline-table td:nth-child(7) .stage-count { border-color: #a9b1d6; color: #a9b1d6; }
.pipeline-table td:nth-child(8) .stage-count { border-color: #9ece6a; color: #9ece6a; }
.pipeline-table td:nth-child(9) .stage-count { border-color: #f7768e; color: #f7768e; }

.pipeline-table .arrow {
  display: inline-block;
  font-weight: bold;
}
.pipeline-table td:nth-child(2) .arrow { color: #bb9af7; }
.pipeline-table td:nth-child(3) .arrow { color: #7aa2f7; }

.reject-stage .stage-count {
    color: #f7768e;
    border-color: #f7768e;
}

.empty-pipeline {
  text-align: center;
  padding: 2rem;
  color: #a9b1d6;
}

/* --- Calls Section --- */
.calls-section-container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 1.5rem;
}

.calls-column {
    background-color: #1e2030;
    padding: 1.5rem;
    border-radius: 8px;
    border: 1px solid #2a2f4e;
    display: flex;
    flex-direction: column;
}

.calls-list {
    margin-top: -0.5rem;
    overflow-y: auto;
    max-height: 400px;
    padding-right: 0.5rem;
}

.calls-list ul {
    list-style: none;
    padding: 0;
    margin: 0;
}

.calls-list li {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0.75rem 0.5rem;
    border-radius: 5px;
    transition: background-color 0.2s ease;
}

.calls-list li:hover {
    background-color: #2a2f4e;
}

.call-info-main {
    display: flex;
    align-items: center;
    gap: 1rem;
}

.call-time {
    font-size: 0.85rem;
    font-weight: bold;
    color: #bb9af7;
    background-color: rgba(187, 154, 247, 0.1);
    padding: 0.25rem 0.5rem;
    border-radius: 4px;
    min-width: 60px;
    text-align: center;
}

.call-details {
    display: flex;
    flex-direction: column;
}

.call-details strong {
    color: #c0caf5;
}

.call-details span {
    font-size: 0.8rem;
    color: #a9b1d6;
}

.btn-call-linkedin {
    color: #565f89;
    padding: 0.5rem;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.btn-call-linkedin:hover {
    color: #7aa2f7;
    background-color: #2a2f4e;
}

.no-calls-message {
    color: #a9b1d6;
    padding: 1rem;
    text-align: center;
    font-style: italic;
}